import React, { useState, useEffect, useRef } from 'react';
import { DatePicker, Button, Space } from 'antd';
import moment, { Moment }  from "moment";

const { RangePicker } = DatePicker;
const dateFormat: string = 'DD.MM.YYYY';

interface ISpecificPeriodSelector {
  setPeriod: any;
  setGroup: any;
  setType: any;
  period: string;
  group: string;
  type: string;
}

const SpecificPeriodSelector = ({setPeriod, setGroup, setType, period, group, type} : ISpecificPeriodSelector): JSX.Element => {
  return (
    <>
      <div>Period:</div>
      <Space>
        <Button onClick={() => setPeriod('today')} type={period === 'today' ? 'primary' : 'default'}>Today</Button>
        <Button onClick={() => setPeriod('week')} type={period === 'week' ? 'primary' : 'default'}>Last Week</Button>
        <Button onClick={() => setPeriod('month')} type={period === 'month' ? 'primary' : 'default'}>Last Month</Button>
      </Space>
      
      <div>Group: </div>
      <Space>
        <Button onClick={() => setGroup('day')} type={group === 'day' ? 'primary' : 'default'}>Day</Button>
        <Button onClick={() => setGroup('weeks')} type={group === 'weeks' ? 'primary' : 'default'}>Week's</Button>
        <Button onClick={() => setGroup('months')} type={group === 'months' ? 'primary' : 'default'}>Month's</Button>
        <Button onClick={() => setGroup('total')} type={group === 'total' ? 'primary' : 'default'}>Total</Button>
      </Space>
      
      <div>Type: </div>
      <Space>
        <Button onClick={() => setType('approved')} type={type === 'approved' ? 'primary' : 'default'}>Approved</Button>
        <Button onClick={() => setType('leads')} type={type === 'leads' ? 'primary' : 'default'}>Leads</Button>
        <Button onClick={() => setType('sendToCS')} type={type === 'sendToCS' ? 'primary' : 'default'}>Send to CS</Button>
      </Space>
    </>
  )
}

enum Period { 
  Today = "today", 
  Week = "week",
  month = "month", 
};

const DateFilter = (): JSX.Element => {
  const [status, setStatus] = useState<boolean>(false);
  const [period, setPeriod] = useState<string>(Period.Week);
  const [group, setGroup] = useState<string>('weeks');
  const [type, setType] = useState<string>('total');
  const [date, setDate] = useState<[Moment, Moment]>([moment(new Date(), dateFormat), moment(new Date(), dateFormat)]);

  const wrapperRef = useRef<any>();

  const onChange  = (dates: any, dateStrings: [string, string]): void  =>  {
      console.log(dates, dateStrings);
      setPeriod('');
      setGroup('');
  };

  const handleToggle = (status:  boolean) => {
    setStatus(status);
  }

  const selectPeriod= (period: string) => {
    const nowDate: Date = new Date;

    if ( period ===  'today') {
      setDate([moment(nowDate, dateFormat), moment(nowDate, dateFormat)]);
    }

    if ( period ===  'week') {
      setDate([moment(nowDate.getDate() - 7, dateFormat), moment(nowDate, dateFormat)]);
    }

    if ( period ===  'month') {
      setDate([moment(new Date(nowDate.getFullYear(), nowDate.getMonth(), 1), dateFormat), moment(new Date(), dateFormat)]);
    }

    setPeriod(period);
  }

  const handleClickOutside = (event: any) => {
    // if (wrapperRef.current && !wrapperRef?.current?.contains(event.target)) {
    //   setStatus(false);
    // }
  };

  useEffect(() => {
    document.addEventListener('click', handleClickOutside, true);
    return () => {
      document.removeEventListener('click', handleClickOutside, true);
    };
  }, []);

  console.log(wrapperRef.current);

  return (
      <RangePicker
        value={date}
        format={dateFormat}
        renderExtraFooter={() => 
          <SpecificPeriodSelector 
            setPeriod={selectPeriod} 
            setGroup={setGroup} 
            setType={setType}
            period={period} 
            group={group} 
            type={type}
          />}
        onChange={onChange}
        open={status}
        onOpenChange={handleToggle}
        ref={wrapperRef}
      />
  )
};

export { DateFilter };